@import (less) "color.less";
:host {
    position: relative;
    display: block;
}
:host(.ng-touched.ng-invalid, .nbp-show-validation.ng-invalid) {
    .dropdown-input-box {
        border: 0.125em solid @red !important;
        -webkit-box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        &:focus {
            border: 0.125em solid @red !important;
        }
    }
}

:host(.ng-dirty.ng-invalid, .nbp-show-validation.ng-invalid) {
    .dropdown-input-box {
        border: 0.125em solid @red !important;
        -webkit-box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        &:focus {
            border: 0.125em solid @red !important;
        }
    }
}

.highlight-default {
    border: 0.125em solid @yellow !important;
    outline: 0;
    box-shadow: none !important;
}

.dropdown-container {
    display: inline-block;
    position: relative;
    width: 100%; // margin-bottom: 1.563em;
    ul {
        opacity: 1;
        background: @white;
        border-top-width: 0;
        border-right-color: @darkest_gray;
        border-right-style: solid;
        border-right-width: 0.0625em;
        border-bottom-color: @darkest_gray;
        border-bottom-style: solid;
        border-bottom-width: 0.0625em;
        border-left-color: @darkest_gray;
        border-left-style: solid;
        border-left-width: 0.0625em;
        border-radius: 0 0 0.125em 0.125em;
        list-style: none;
        margin: 0 0 0.625em 0;
        padding: 0;
        position: absolute;
        width: 100%; 
        z-index: 1000;
        overflow-y: auto;
        max-height: 12.8125em;
        >li {
            cursor: pointer;
            min-height: 3.125em;
            padding: .75em .625em .5em .625em;
            border-bottom: 0.0625em solid @light_gray;
        }
        >li:hover {
            background: @lightest_green;
          }
        >li.preselected{
            background: @lightest_green;
        }
        >li.preselected.active{
          box-shadow: inset 0 0 1em @green;
        }
        >li.active {
            background: @light_green;
        }
        >li.last {
            border-bottom: none;
        }
    }
    ul.ng-hide-add {
        transition: all ease-out 0.25s;
    }
    ul.ng-hide-remove {
        transition: all ease-out 0.25s;
    }
    ul.ng-hide {
        opacity: 0;
        max-height: 0;
    }
}

.dropdown-inner-container {
    position: relative;
    z-index: 999;
    &.collapse-disabled {
      pointer-events: none;
  }
}

.dropdown-label {
    color: @gray;
    font-size: 1em;
    margin: 0 0 0 0.750em !important;
}

.dropdown-input-box {
    // margin: 0.625em 0 0 0;
    margin: 0;
    border-collapse: separate;
    //modificareply
    //box-shadow: inset 0px 0px 4px 0px #323232 !important;
    /*unselectable text*/
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    /*prima soluzione per evitare testo troppo lungo nelle combobox
    padding: 0.375em 3em 0.375em 1em;*/
    /*oppure con ellipse*/
    padding: 0.625em;//0.375em 2.3em 0.375em 1em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: @darkest_gray;
    border: 0.0625em solid @darkest_gray;
    border-radius: 0.125em;
    font-size: 1em !important;
    height: 3.125em;
    &:focus {
        border: 0.125em solid @light_green;
        outline: 0;
        box-shadow: none !important;
    }
    &::-ms-clear {
        display: none;
    }
    &:disabled {
        background-color: @white !important;
        cursor: default;
        box-shadow: none;
    }
    &.input-box-disabled {
        background-color: @white !important;
        border: 0.0625em solid @gray;
        opacity: 0.5 !important;
        cursor: not-allowed;
    }
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: @gray;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: @gray;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: @gray;
}

.dropdown-input-box-green {
    border: 0.125em solid @light_green;
    outline: 0;
    box-shadow: none !important;
}

.dropdown-input-box-disabled {
    background-color: @white !important;
    opacity: 0.5 !important;
}

.dropdown-button {
    display: table;
    position: absolute;
    top: 0;
    right: 0;
    width: 2.5em;
    height: 3.125em;
    text-align: center;
    color: @light_gray;
    border-color: transparent;
    background-color: transparent !important;
    //span {
        //vertical-align: middle;
    //}
    &[role=""] {
        cursor: default;
        opacity: 0.5;
    }
    &.collapse-disabled .dropdown-button-border {
        border-left: 0.0625em solid @gray !important;
    }
    &.collapse-disabled .triangolo-bottom {
      border-style: solid;
      border-width: 0.5em 0.40625em;
      border-color: @gray transparent transparent transparent;
  }
    &.collapse-disabled .triangolo-top {
        border-style: solid;
        border-width: 0 0.40625em 0.5em 0.40625em;
        border-color: transparent transparent @gray transparent;
    }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  
    .dropdown-button span{
      position:absolute;
    }

    .dropdown-button.collapse-disabled .triangolo-top {
      transform: translateY(-50%);
      top:50%;
    }

    .dropdown-button.collapse-disabled .triangolo-bottom {
      transform: translateY(-50%);
      top:50%;
    }

    .triangolo-top {
      transform: translateY(-50%);
      top:50%;
  }

  .triangolo-bottom {
      transform: translateY(-50%);
      top:50%;
  }

}

.dropdown-button-border {
    position: absolute;
    display: inline-block;
    height: 60%;
    top: 20%;
    left: 0%;
    border-left: 0.0625em solid @darkest_gray;
}

.has-error {
    .dropdown-input-box {
        border: 0.125em solid @red;
        -webkit-box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        box-shadow: inset 0 0.0625em 0.0625em rgba(0, 0, 0, .075);
        &:focus {
            border: 0.125em solid @red;
        }
    }
}

.dropdown-error-message {
    color: @red;
    font-size: 1em;
    margin-top: 0.625em;
    margin-bottom: 0.625em;
    opacity: 1;
}

.dropdown-error-message.ng-hide-add {
    transition: all ease-out 0.25s;
}

.dropdown-error-message.ng-hide-remove {
    transition: all ease-out 0.25s;
}

.dropdown-error-message.ng-hide {
    opacity: 0;
    margin-top: -0.625em;
}

.triangolo-top {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0.625em 0.625em 0.625em;
    border-color: transparent transparent @green transparent;
    display: inline-block !important; //margin-top: 30%;
}

.triangolo-bottom {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0.625em 0.625em 0 0.625em;
    border-color: @green transparent transparent transparent;
    display: inline-block !important; //margin-top: 30%;
}

.yellow-border {
    border: 0.125em solid @yellow;
}

:host-context(.contrasto-on) {
  :host(.ng-invalid.nbp-show-validation) {
    .dropdown-input-box {
      border-color: @black !important;
      &:focus {
        border-color: @black !important;
      }
    }
  }

  .highlight-default {
    border-color: @black !important;
  }

  .dropdown-container {
    ul {
      background: @white;
      border-right-color: @black;
      border-bottom-color: @black;
      border-left-color: @black;
      >li {
          border-bottom-color: @white;
      }
      >li:hover {
          background: @black;
          color: @white;
      }
      >li.active {
          background: @black;
          color: @white;
      }
    }
  }

  .dropdown-label {
    color: @black;
  }

  .dropdown-input-box {
    color: @black;
    border-color: @black;
    &:focus {
        border-color: @black;
    }
    &:disabled {
        background-color: @white !important;
    }
    &.input-box-disabled {
        background-color: @white !important;
        border-color: @black;
    }
  }

  .dropdown-input-box-green {
    border-color: @black;
  }

  .dropdown-input-box-disabled {
    background-color: @white !important;
  }

  .dropdown-button {
    color: @black;
    &.collapse-disabled .dropdown-button-border {
        border-left-color: @black !important;
    }
    &.collapse-disabled .triangolo-bottom {
        border-top-color: @black;
    }
    &.collapse-disabled .triangolo-top {
        border-bottom-color: @black;
    }
  }

  .dropdown-button-border {
    border-left-color: @black;
  }

  .has-error {
    .dropdown-input-box {
        border-color: @black;
        &:focus {
            border-color: @black;
        }
    }
  }

  .dropdown-error-message {
    color: @black;
  }

  .triangolo-top {
    border-bottom-color: @black;
  }

  .triangolo-bottom {
    border-top-color: @black;
  }

  .yellow-border {
    border-color: @white;
  }
}

